import userbar from './common/userbar'
import header_menu from './common/header.menu'
import user__dialog from "@/components/user/user.dialog";
import header_usercenter from './common/header.usercenter'
import search_bar from '@/components/common/search.bar'
import to_top from '@/components/common/to.top'
import lang_select from '@/components/common/lang.select'
import ads_menu from "@/components/ads/ads_menu";
import header_top_menu from '@/components/common/header.top.menu'

export default {
  name: 'appheader',
  components: {
    header_top_menu,
    user__dialog,
    lang_select,
    ads_menu,
    header_menu,
    search_bar,
    header_usercenter,
    to_top
  },
  computed: {
    download_app_url: function () {
      var x91app_conf = this.$ads_object.text['x91.18r'];
      console.log('x91app_conf:', x91app_conf);
      if (x91app_conf) {
        console.log("x91app_conf['url']:", x91app_conf['url']);
        return x91app_conf['url'];
      }
      return '/';
    }
  },
  created() {
    if (this.is_mobile()) {
      this.items.push({
        component: header_usercenter,
        text: 'header_usercenter'
      });
      this.items.push({
        component: header_menu,
        text: 'header_menu'
      });
    } else {
      this.items.push({
        component: header_menu,
        text: 'header_menu'
      });
      this.items.push({
        component: header_usercenter,
        text: 'header_usercenter'
      });
    }

    window._header = this;
    window.addEventListener('scroll', this.scrollToTop)
    console.log('header create');
    this.$nextTick(() => {
      if (this.$refs.header) {
        console.log('this.$refs.header:', this.$refs.header);
        if (this.$refs.header) {
          console.log('this.$refs.header_menu.offsetHeight:', this.$refs.header.offsetHeight);
          this.header_height = this.$refs.header.offsetHeight;

        }
      }
    })
  },
  destroyed() {
    window.removeEventListener('scroll', this.scrollToTop)
  },
  data() {
    return {
      header_height: 0,
      btnFlag: false,
      search_keyword: '',
      items: [],
      date: null,
      show_user_dialog: false,
      menu_show: false,
      search_show: false,
    }
  }, methods: {
    on_user_center_click: function () {
      this.show_user_dialog = false;
      this.$nextTick(() => {
        this.show_user_dialog = true;
        this.$forceUpdate();
      });
    },
    on_search: function () {
      console.log('[on_search]search_keyword:', this.search_keyword);
      if (this.search_keyword) {
        var url = '/search/' + this.search_keyword;
        this.$router.push(url);
      }
      return false;
    },
    backTop() {
      const that = this
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5)
        document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed
        if (that.scrollTop === 0) {
          clearInterval(timer)
        }
      }, 16)
    },
    scrollToTop() {
      const that = this
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      that.scrollTop = scrollTop
      if (that.scrollTop > 0) {
        this.btnFlag = true
      } else {
        this.btnFlag = false
      }
    },
    on_menu: function () {
      console.log('call on_menu ');
      this.menu_show = !this.menu_show;
    },
    on_menu_mask: function () {
      this.menu_show = false;
      this.search_show = false;

    },
    on_search_click: function () {
      this.search_show = !this.search_show;
    },
    on_search_mask_click: function () {
      this.search_show = false;
    }
  }
}
